<template>
	<div class="jggcj">
		<ul>
			<li class="active">1</li>
			<li>2</li>
			<li>3</li>
			<li>4</li>
			<li>5</li>
			<li>6</li>
			<li>7</li>
			<li>8</li>
		</ul>
		<button class="but">点击抽奖</button>
	</div>
</template>

<script type="text/javascript">
	export default{
		mounted(){
			let ul = document.querySelector('ul');
			let buttons = document.querySelector('.but');
			let li = ul.querySelectorAll('li');

			let i = 0;
			let count = 0;
			let totalcount = 3;
			let speed = 500;
			let minspeed = 500;
			let timer;
			let isClick = true;
			let index = Math.floor(Math.random() * 7);


			function roll() {
				speed -= 50;
				if (speed <= 10) {
					speed = 10;
				}
				for (let j = 0;j<li.length;j++){
					li[j].classList.remove('active');
				}
				i++;
				if (i >= li.length) {
					i = 0;
					count++;
				}
				li[i].classList.add('active');
				console.log(index)

				if (count >= totalcount &&  i == index) {
					clearTimeout(timer);
					isClick = true;
					speed = minspeed;
					setTimeout(() =>{
						alert('恭喜你获得了' + (i + 1));
					},500)
				}else{
					timer = setTimeout(roll,speed);
					if (count>= totalcount -1 || speed <= 50) {
						speed += 100;
					}
				}

			}
			buttons.onclick = function(){
				if (isClick) {
					i = 0;
					count = 0;
					roll();
					isClick = false;
					index = Math.floor(Math.random() * 7)
				}
			}


		}
	}
</script>

<style lang="scss">
	.jggcj{
		width: 100%;
		height: 0;
		padding-top: 100%;
		position: relative;
		ul{
			width: 100%;
			height: 100%;
			position:absolute;
			top: 0;
			li{
				width: 30%;
				height: 30%;
				background:#DEB887;
				position:absolute;
				font-size: .32rem;
				color: white;
				display: flex;
				justify-content: center;
				align-items: center;
				box-sizing: border-box;
				&:nth-of-type(1){
					top: 0;
					left: 0;
				}
				&:nth-of-type(2){
					top: 0;
					left: 35%;
				}
				&:nth-of-type(3){
					top: 0;
					left: 70%;
				}
				&:nth-of-type(4){
					top: 35%;
					left: 70%;
				}
				&:nth-of-type(5){
					top: 70%;
					left: 70%;
				}
				&:nth-of-type(6){
					top: 70%;
					left: 35%;
				}
				&:nth-of-type(7){
					top: 70%;
					left: 0%;
				}
				&:nth-of-type(8){
					top: 35%;
					left: 0%;
				}
				&.active{
					border:.04rem solid red;
				}
			}
		}
		button{
			position:absolute;
			width: 30%;
			height: 30%;
			top: 35%;
			left: 35%;
			background:black;
			color: white;
		}
	}
</style>